#main .thing h4 {
  font-size: 1.7rem;
  margin-top: 0;
  margin-bottom: 12px; }

#main .thing p {
  font-size: 1.2rem; }

#main .thing img {
  width: 3rem;
  height: 4rem;
  margin-top: 0.85rem;
  margin-right: 1rem;
  float: left; }

#main ul {
  list-style-type: disc; }
  #main ul li p {
    display: inline; }

#main div.one-half.column {
  align-self: normal; }

#main img.blog {
  margin: 1rem; }

#header #brand img {
  display: none; }

#header ul {
  margin: auto; }

#header.home .nav {
  display: inline-block; }

#header.home #menu-toggle {
  display: none; }

#header.home #brand {
  display: inline-block; }

#header.home nav {
  display: inline-block; }

#header.home #brand {
  display: none; }

#header.home > div {
  text-align: center; }

#header.home nav {
  float: none; }
  #header.home nav span.nav-name {
    display: inline-block; }

@media (max-width: 1275px) {
  #header.home nav .navbar-nav li {
    padding-left: 0;
    padding-right: 0; }
    #header.home nav .navbar-nav li span.nav-name {
      display: none; } }

@media (max-width: 800px) {
  #header.home #menu-toggle {
    display: inline-block;
    margin-top: 0.9rem;
    z-index: 9999; }
  #header.home nav {
    width: 100%;
    display: none;
    margin-top: 8px; }
    #header.home nav .navbar-nav {
      width: 100%; }
      #header.home nav .navbar-nav li {
        text-align: left;
        display: block;
        padding-left: 12px;
        padding-right: 12px;
        width: 100%; }
        #header.home nav .navbar-nav li span.nav-name {
          display: inline; }
        #header.home nav .navbar-nav li a {
          width: 100%;
          display: block; }
  #header.home .navbar-icons {
    display: block; }
    #header.home .navbar-icons li {
      display: inline-block;
      padding-left: 1rem; } }

#doodles .controls {
  display: flex;
  justify-content: space-evenly;
  z-index: 3; }
  #doodles .controls div {
    cursor: pointer;
    height: 2rem;
    width: 2rem;
    font-size: 1rem;
    padding: 10px 10px 10px 10px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center; }
    #doodles .controls div:hover {
      background-color: rgba(127, 127, 127, 0.2); }

#doodles .slider {
  padding: 0;
  margin: 0 auto;
  height: 35rem;
  max-height: 35rem;
  overflow: hidden; }
  #doodles .slider .slide {
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    width: 100%;
    z-index: 1;
    -webkit-transition: opacity 1s;
    -moz-transition: opacity 1s;
    -o-transition: opacity 1s;
    transition: opacity 1s; }
    #doodles .slider .slide img {
      object-fit: contain;
      width: 100%;
      height: 30rem; }
      #doodles .slider .slide img.expand {
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh; }
    #doodles .slider .slide div {
      text-align: center;
      height: 1rem;
      margin-top: 1rem; }
  #doodles .slider .showing {
    opacity: 1;
    z-index: 2; }

#themer {
  display: block;
  margin: 0 auto 1rem auto;
  font-size: 0.8rem;
  height: inherit;
  line-height: inherit;
  text-transform: lowercase; }
  #themer:hover {
    color: inherit; }
